﻿﻿﻿﻿﻿'use client';

import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Package, ShoppingCart, Users, Wallet } from 'lucide-react';

export default function DashboardPage() {
  return (
    <div className="space-y-8 rounded-3xl border border-slate-800/70 bg-gradient-to-b from-slate-900/80 via-slate-950 to-slate-950 shadow-[0_22px_60px_rgba(15,23,42,0.95)] p-6 md:p-8">
      {/* HEADER */}
      <header className="flex flex-col gap-1">
        <h1 className="text-3xl font-bold tracking-tight text-white">Dashboard</h1>
        <p className="text-slate-300">
          Bienvenido al sistema de punto de venta. Aquí verás un resumen rápido de tu negocio.
        </p>
      </header>

      {/* CARDS RESUMEN */}
      <section className="grid grid-cols-1 gap-6 md:grid-cols-2 lg:grid-cols-4">
        <Card variant="default" className="hover:shadow-xl transition-shadow bg-slate-900/60 border border-slate-700/70 backdrop-blur-xl">
          <CardContent className="flex items-center justify-between py-4">
            <div>
              <p className="text-sm text-slate-300">Ventas hoy</p>
              <p className="mt-2 text-3xl font-bold text-white">0</p>
            </div>
            <div className="flex h-12 w-12 items-center justify-center rounded-full bg-primary-50 text-primary-600">
              <ShoppingCart className="h-6 w-6" />
            </div>
          </CardContent>
        </Card>

        <Card variant="default" className="hover:shadow-xl transition-shadow bg-slate-900/60 border border-slate-700/70 backdrop-blur-xl">
          <CardContent className="flex items-center justify-between py-4">
            <div>
              <p className="text-sm text-slate-300">Productos</p>
              <p className="mt-2 text-3xl font-bold text-white">0</p>
            </div>
            <div className="flex h-12 w-12 items-center justify-center rounded-full bg-primary-50 text-primary-600">
              <Package className="h-6 w-6" />
            </div>
          </CardContent>
        </Card>

        <Card variant="default" className="hover:shadow-xl transition-shadow bg-slate-900/60 border border-slate-700/70 backdrop-blur-xl">
          <CardContent className="flex items-center justify-between py-4">
            <div>
              <p className="text-sm text-slate-300">Clientes</p>
              <p className="mt-2 text-3xl font-bold text-white">0</p>
            </div>
            <div className="flex h-12 w-12 items-center justify-center rounded-full bg-primary-50 text-primary-600">
              <Users className="h-6 w-6" />
            </div>
          </CardContent>
        </Card>

        <Card variant="default" className="hover:shadow-xl transition-shadow bg-white/5 border border-white/10 backdrop-blur-xl">
          <CardContent className="flex items-center justify-between py-4">
            <div>
              <p className="text-sm text-slate-300">Ingresos</p>
              <p className="mt-2 text-3xl font-bold text-white">S/ 0.00</p>
            </div>
            <div className="flex h-12 w-12 items-center justify-center rounded-full bg-primary-50 text-primary-600">
              <Wallet className="h-6 w-6" />
            </div>
          </CardContent>
        </Card>
      </section>

      {/* ACTIVIDAD RECIENTE */}
      <section>
        <Card variant="outline" className="border-slate-700/70 bg-slate-900/60 backdrop-blur-xl">
          <CardHeader>
            <CardTitle>Actividad reciente</CardTitle>
          </CardHeader>
          <CardContent className="flex flex-col items-center justify-center gap-3 py-10 text-center">
            <div className="flex h-12 w-12 items-center justify-center rounded-full bg-white/10 text-slate-200">
              <ShoppingCart className="h-6 w-6" />
            </div>
            <p className="font-medium text-slate-200">Todavía no hay actividad reciente</p>
            <p className="text-sm text-slate-400">
              Cuando registres ventas, clientes o productos, verás aquí un resumen de tus últimos movimientos.
            </p>
          </CardContent>
        </Card>
      </section>
    </div>
  );
}
